
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Masker from 'vux/src/components/masker/index.vue'

export default {
components: {
ViewBox,
XHeader,
Group,
Masker,
Cell
},
data () {
return {
index: 0
}
},
computed: {
isLogin () {
return this.$store.state.userInfo.token !== ''
}
},
mounted () {
},
methods: {
shareCodePic () {

let self = this
let data = {
action: 'jump_qr_dialog'
}
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
} else {
if (this.$store.state.userInfo.isAuth === true) {
self.$store.dispatch('normalRequest', data).then(res => {
})
} else {
this.toVerfiedPage()
}
}
},
jumpNormalQs () {
let self = this
let token = self.$store.state.userInfo.token
let path = self.$store.state.baseUrl + '/app/xsyd/commonProblem.html?' + token
let data = {
action: 'jump_web_show',
path: path,
params: {
name: '常见问题'
}
}
self.$store.dispatch('normalRequest', data).then(res => {
})
},
consultService () {
let self = this
let token = self.$store.state.userInfo.token
let path = self.$store.state.baseUrl + '/app/xsyd/contactUs.html?' + token
let data = {
action: 'jump_web_show',
path: path,
params: {
name: '咨询服务'
}
}
self.$store.dispatch('normalRequest', data).then(res => {
})
},
toSuggest () {
if (this.$store.state.userInfo.token === '') {
this.$router.push('/login')
} else {
if (this.$store.state.userInfo.isAuth === true) {
this.$router.push('/suggest')
} else {
this.toVerfiedPage()
}
}
},
exit () {

let self = this
let token = self.$store.state.userInfo.token
let data = {
action: 'exit_request'
}
if (token !== '') {

this.$vux.loading.show({
text: 'Loading'
})
self.$store.dispatch('normalRequest', data).then(res => {

self.$store.commit('INIT_HEAD_IMG', {avatar: ''})
self.$store.commit('INIT_USER_INFO', res)

this.$vux.loading.hide()
this.$vux.toast.text('退出登录成功')
self.$router.replace('/my')
self.$store.state.tabItem = 2
})
} else {
this.$vux.loading.hide()
this.$vux.toast.text('您未登录，无法退出')
}
}
}
}



